.form {
	width: 300px;
	border: 1px solid lightblue;
	border-radius: 3px;
	padding: 10px;
}
.form-label {
	text-align: right;
}
.form-sing {
	float: left;
	padding-right: 20px;
}
.form-input {
	width: 200px;
	float: right;
}
.error {
	border-color: red;
}
// --------------- autocomplite
.resultInput {
	position : absolute;
	box-sizing : border-box;
	background : #fff;
	border : 1px solid gray;
	padding : 10px;
	cursor : pointer;
}
.zeroHeight {
	padding: 0;
	height: 0;
	border: none;
}
.draken {
	background-color: lightgray;
}
// --------------- tabs
.tabs {
	width: 600px;
}
.tabs-tab {
	float: left;
	width: 25%;
	box-sizing: border-box;
	border: 1px solid lightgray;
	border-bottom: none;
	padding: 20px;
	text-align: center;
	cursor: pointer;
	transiton: all .5s;
	&:hover {
		background-color: lightgray;
		border: 1px solid lightgray;
	}
}
.tabs-tab:first-child {
	border-top-left-radius: 10px;
}
.tabs-tab:last-child {
	border-top-right-radius: 10px;
}
.tabs-fields {
	position: relative;
	top: -2px;
	background-color: #fff;
	z-index: -1;
}
.tabs-field {
	display: none;
	border: 2px solid green;
	padding: 10px;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}
.tabs-tab.active {
	border: 2px solid green;
	border-bottom: none;
	background-color: lightgreen;
}
.tabs-field.active {
	display: block;
	background-color: lightgreen;
}